<template>
  <div class="page page-roadshow-list">
    <div class="list-options">
      <!--  :labelCol="{ span: 10 }" :wrapperCol="{ span: 10 }"  -->
      <div class="flex items-center">
        <div class="flex items-center">
          <span>关键字：</span>
          <a-input size="small" v-model="params.keyword" style="width: 200px" placeholder="权限名称/编号" />
        </div>
        <a-button style="margin-left: 50px" type="primary" icon="search" size="small" :loading="loading" @click="handleSearch">查询</a-button>
      </div>
    </div>
    <a-divider></a-divider>
    <a-space class="v-mb-md">
      <a-button type="primary" size="small" style="min-width: 60px" @click="handleAdd">新增</a-button>
      <a-button type="primary" size="small" style="min-width: 60px" @click="handleDelBatch">删除</a-button>
    </a-space>
    <div class="list-container">
      <a-table
        :columns="columns"
        :dataSource="list"
        :pagination="{
          showQuickJumper: true,
          showSizeChanger: true,
          hideOnSinglePage: false,
          pageSizeOptions: ['10', '20', '30', '50'],
          total: pagination.totalRow,
          pageSize: pagination.pageSize,
          current: pagination.pageNumber,
          onChange: onPageChange,
          onShowSizeChange: onPageChange,
        }"
        :rowSelection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"
        :scroll="{ x: '100%' }"
        :loading="loading"
        tableLayout="auto"
        class="text-no-wrap"
        rowKey="id"
        size="small"
      >
        <template slot="action" slot-scope="text, record">
          <a-button class="text-red" icon="edit" size="small" title="编辑" type="link" @click="handleEdit(record)"></a-button>
          <a-popconfirm placement="top" okText="确认" cancelText="取消" @confirm="handleDel(record)">
            <template slot="title">
              <div>确认删除 ?</div>
            </template>
            <a-icon slot="icon" type="info-circle" style="color: red" />
            <a-button class="text-red" icon="close" size="small" title="删除" type="link"></a-button>
          </a-popconfirm>
        </template>
      </a-table>
    </div>
    <!-- 编辑/新增权限 -->
    <a-modal :confirmLoading="modalSaving" :visible="modalVisible" :width="400" @cancel="modalVisible = false" @ok="handleSave" centered okText="保存" :title="modalTitle">
      <a-form-model ref="ruleForm" :model="form" :rules="formRules" :label-col="{ span: 6 }" :wrapper-col="{ span: 16 }">
        <a-form-model-item label="权限名称" prop="name">
          <a-input v-model="form.name" placeholder="输入权限名称" />
        </a-form-model-item>
        <a-form-model-item label="权限编号" prop="code">
          <a-input v-model="form.code" placeholder="输入权限编号" />
        </a-form-model-item>
        <a-form-model-item label="权限类型" prop="type">
          <a-select allowClear v-model="form.type" placeholder="选择权限类型" style="width: 100%">
            <a-select-option :value="0">权限类型1</a-select-option>
            <a-select-option :value="1">权限类型2</a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="父级权限" prop="parent">
          <a-select allowClear v-model="form.parent" placeholder="选择父级权限" style="width: 100%">
            <a-select-option :value="0">权限1</a-select-option>
            <a-select-option :value="1">权限2</a-select-option>
          </a-select>
        </a-form-model-item>
      </a-form-model>
    </a-modal>
  </div>
</template>
<script>
const form = {
  id: undefined,
  name: '',
  code: '',
  type: undefined,
  parent: undefined,
}
export default {
  data() {
    return {
      form: { ...form },
      formRules: {
        name: [{ required: true, message: '请输入权限名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入权限编码', trigger: 'blur' }],
        type: [{ required: true, message: '请选择权限类型', trigger: 'blur' }],
      },
      modalTitle: '新增权限',
      modalVisible: false,
      modalSaving: false,
      loading: false,
      list: [],
      selectedRowKeys: [],
      selectedRows: [],
      pagination: {
        totalPage: 1,
        pageNumber: 1,
        pageSize: 10,
        totalRow: 0,
      },
      params: {
        keyword: '',
        status: undefined,
      },
      columns: [
        {
          title: '序号',
          dataIndex: 'id',
          width: 50,
          align: 'center',
          customRender: (text, record, index) => index + 1,
        },
        {
          title: '权限名称',
          dataIndex: 'name',
          align: 'left',
        },
        {
          title: '权限编号',
          dataIndex: 'code',
        },
        {
          title: '权限类型',
          dataIndex: 'type',
          // width: 100,
        },
        {
          title: '创建时间',
          dataIndex: 'createTime',
          align: 'center',
          width: 122,
        },
        {
          title: '操作',
          dataIndex: 'action',
          width: 100,
          fixed: 'right',
          align: 'center',
          scopedSlots: { customRender: 'action' },
        },
      ],
    }
  },
  computed: {
    user: (vm) => vm.$store.state.user.user,
  },
  methods: {
    handleSearch(e) {
      this.load(1, 10)
    },
    onStatusChange() {},
    load(pageNumber = 1, pageSize = 10) {
      this.loading = true
      setTimeout(() => {
        let list = []
        for (let index = 0; index < pageSize; index++) {
          let idx = (pageNumber - 1) * pageSize + index
          list.push({
            id: idx,
            name: `权限名称${index}`,
            createTime: '2021-04-21 10:' + (60 - index),
            code: 'XCSDDGDS',
            type: 'JREGRG',
          })
        }
        let totalRow = 24
        this.pagination = {
          // firstPage,
          // lastPage,
          pageNumber: pageNumber,
          pageSize,
          // totalPage,
          totalRow,
        }
        this.list = list
        this.loading = false
      }, 500)
    },
    onPageChange(pageNumber, pageSize) {
      this.load(pageNumber, pageSize)
    },
    onPageSizeChange(pageNumber, pageSize) {
      this.load(pageNumber, pageSize)
    },
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys
      this.selectedRows = [...selectedRows]
    },
    handleDel(record) {
      console.log(record)
    },
    handleDelBatch() {
      let vm = this
      let selectedRowKeys = vm.selectedRowKeys
      if (selectedRowKeys.length === 0) {
        this.$message.warning('请选择至少一条记录')
        return
      }
      this.$confirm({
        title: '确认删除所选择的记录？',
        cancelText: '',
        icon: 'info-circle',
        onOk() {
          let { pageNumber, pageSize } = vm.pagination
          vm.load(pageNumber, pageSize)
        },
      })
    },
    handleAdd() {
      this.form = { ...form }
      this.modalTitle = '新增权限'
      this.modalVisible = true
    },
    handleEdit(record) {
      this.form = { ...record }
      this.modalTitle = '编辑权限'
      this.modalVisible = true
    },
    handleSave() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.modalSaving = true
          setTimeout(() => {
            this.modalSaving = false
            this.modalVisible = false
            let { pageNumber, pageSize } = this.pagination
            this.load(pageNumber, pageSize)
          }, 500)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
  },
  mounted() {
    let { pageNumber, pageSize } = this.pagination
    this.load(pageNumber, pageSize)
  },
}
</script>
